
.container{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    
}
.bubbles{
    position: relative;
    display: flex;
}
.bubbles span{
    position: relative;
    width: 15px;
    height: 15px;
    background: #4fc3dc;
    margin: 0 4px;
    border-radius: 50%;
    box-shadow: 0px 0px 5px #4fc3dc44,
    0 0 10px #4fc3dc,
    0 0 15px #4fc3dc;
    
    animation: animate 15s linear infinite;
    animation-duration: calc(125s / var(--i));
    
}
.bubbles span:nth-child(even){
    background: #ff2d75;
    box-shadow: 0 0 0 3px #ff2d7544,
    0 0 0 3px #ff2d75,
    0 0 0 3px #ff2d75;

}
@keyframes animate{
    0%{
        transform: translateY(100vh) scale(0);
    }
    100%{
        transform: translateY(-10vh) scale(1);
    }

}